          <td class="label-cell">{game.img_url}<br/><img alt="" src={game.img_url} width="80" /></td>
        </tr>
        {/each}
        
      </tbody>
    </table>
  </div>
  
</Page>
<script>
  import { NavLeft, NavTitle, NavRight, Link, Row, Col, Chip, List, ListItem, Page, Navbar, Block, BlockTitle } from 'framework7-svelte';
  import { onMount } from 'svelte';
  import dataClient from '../stores/dataClient.js';
  

  export let f7router;

  onMount(async () => {
    await dataClient.getGameCount();
    await dataClient.getGameList();
  });
  
