//core 
import { writable, derived, readable } from 'svelte/store';

const {LoginPromiseClient} = require('../js/esm_datastore/login/login_grpc_web_pb.js');
const {SignInRequest} = require('../js/esm_datastore/login/login_pb.js');

const {UserPromiseClient} = require('../js/esm_datastore/user/user_grpc_web_pb.js');
const {ChangePasswordRequest} = require('../js/esm_datastore/user/user_pb.js');
import {GetCountRequest as GetUserCountRequest} from '../js/esm_datastore/user/user_pb.js';
import {ListRequest as UserListRequest} from '../js/esm_datastore/user/user_pb.js';

const {GreeterPromiseClient} = require('../js/esm_datastore/hello/helloworld_grpc_web_pb.js');
const {HelloRequest} = require('../js/esm_datastore/hello/helloworld_pb.js');

const {GamePromiseClient} = require('../js/esm_datastore/game/game_grpc_web_pb.js');
import {GetCountRequest as GetGameCountRequest} from '../js/esm_datastore/game/game_pb.js';
import {ListRequest as GameListRequest} from '../js/esm_datastore/game/game_pb.js';

const {Item_PromiseClient} = require('../js/esm_datastore/item/item_grpc_web_pb.js');
import {GetCountRequest as GetItemCountRequest} from '../js/esm_datastore/item/item_pb.js';
import {ListRequest as ItemListRequest} from '../js/esm_datastore/item/item_pb.js';

const {PrizePromiseClient} = require('../js/esm_datastore/prize/prize_grpc_web_pb.js');
import {GetCountRequest as GetPrizeCountRequest} from '../js/esm_datastore/prize/prize_pb.js';
import {ListRequest as PrizeListRequest} from '../js/esm_datastore/prize/prize_pb.js';

const {TournamentPromiseClient} = require('../js/esm_datastore/tournament/tournament_grpc_web_pb.js');
import {GetCountRequest as GetTournamentCountRequest} from '../js/esm_datastore/tournament/tournament_pb.js';
import {ListRequest as TournamentListRequest} from '../js/esm_datastore/tournament/tournament_pb.js';


const {WinnerPromiseClient} = require('../js/esm_datastore/winner/winner_grpc_web_pb.js');
import {ListRequest as WinnerListRequest} from '../js/esm_datastore/winner/winner_pb.js';


const serviceURL = 'https://esm.aadi.my/service';

const dataClient = () => {

	const state = {

    isLoggedIn: false,
    username: "",
    password: "",
    jwtToken: "",
    loginClient: new LoginPromiseClient(serviceURL, null, null),
    loginRequest: new SignInRequest(),

    userClient: new UserPromiseClient(serviceURL, null, null),
    changePasswordRequest: new ChangePasswordRequest(),
    getUserCountRequest: new GetUserCountRequest(),
    userListRequest: new UserListRequest(),

    helloClient: new GreeterPromiseClient(serviceURL, null, null),
    helloRequest: new HelloRequest(),

    gameClient: new GamePromiseClient(serviceURL, null, null),
    getGameCountRequest: new GetGameCountRequest(),
    gameListRequest: new GameListRequest(),

    itemClient: new Item_PromiseClient(serviceURL, null, null),
    getItemCountRequest: new GetItemCountRequest(),
    itemListRequest: new ItemListRequest(),

    prizeClient: new PrizePromiseClient(serviceURL, null, null),
    getPrizeCountRequest: new GetPrizeCountRequest(),
    prizeListRequest: new PrizeListRequest(),

    tournamentClient: new TournamentPromiseClient(serviceURL, null, null),
    getTournamentCountRequest: new GetTournamentCountRequest(),
    tournamentListRequest: new TournamentListRequest(),


    winnerClient: new WinnerPromiseClient(serviceURL, null, null),
    winnerListRequest: new WinnerListRequest(),


    users: [],
    userCount: {
      total: 0,
      active: 0,
      blocked: 0,
    },

    games: [],
    gameCount: {
      total: 0,
      draft: 0,
      published: 0,
      archived: 0,
    },

    items: [],
    itemCount: {
      total: 0,
      draft: 0,
      published: 0,
      archived: 0,
    },

    prizes: [],
    prizeCount: {
      total: 0,
      draft: 0,
      published: 0,
      archived: 0,
    },

    tournaments: [],
    tournamentCount: {
      total: 0,
      draft: 0,
      published: 0,
      archived: 0,
    },

	}
	

	const { subscribe, set, update } = writable(0)

	const methods = {

			async init() {

				set(state);
      },
      

      /***
       * User & Login
       */
      async signIn() {
    
        let request = state.loginRequest;
        request.setUsername(state.username);
        request.setPassword(state.password);
    
        try {
          const response = await state.loginClient.signIn(request, {'authorization': 'Bearer before-login-token'});
          if (response == "query returned an unexpected number of rows" || response == "Invalid Password.") {
            alert("Invalid Login");
          } else {
            state.jwtToken = response;
            state.isLoggedIn = true;
          }   
        } catch (err) {
          alert(`Error code = ${err.code}, message = "${err.message}"`);
          console.log(err);
        }
        
        update(state => state);
      },
    

      async sayHello() {

        let request = state.helloRequest;
        request.setName('World');
    
        try {
          const response = await state.helloClient.sayHello(request, {'authorization': state.jwtToken});
          if (response)
            state.isLoggedIn = true;
        } catch (err) {
          alert(`Unexpected error for sayHello: code = ${err.code}, message = "${err.message}.\n\nServer rejected session.  Try Refresh browser and Login again."`);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);

      },

      async getUserCount() {

        for (let i=0; i<3; i++) {
          
          let request = state.getUserCountRequest;
          request.setId(i);
      
          try {
            const response = await state.userClient.getCount(request, {'authorization': state.jwtToken});

            switch(i) {
              case 0:
                state.userCount.total = response; break;
              case 1:
                state.userCount.active = response; break;
              case 2:
                state.userCount.blocked = response; break;
            }
          } catch (err) {
            alert(err.message);
            console.log(err);
            state.isLoggedIn = false;
          }
          
        }
        update(state => state);
        
      },

      async getUserList() {
          
        let request = state.userListRequest;
        request.setLimit(20);
        request.setOffset(0);

        try {
          const response = await state.userClient.list(request, {'authorization': state.jwtToken});

          state.users = [];
          for (let item of response.getResultList()) {
            state.users = [...state.users,  {
              id: item.getId(),
              username: item.getUsername(),
              email: item.getEmail(),
              phone: item.getPhone(),
              firstname: item.getFirstname(),
              lastname: item.getLastname(),
              created_on: item.getCreatedOn(),
              last_login: item.getLastLogin(),
              role_id: item.getRoleId(),
              status: item.getStatus(),
            }];
          }
            
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);
      
      },

      async changePassword(f7router, old_password, new_password) {

        let request = state.changePasswordRequest;
        request.setUsername(state.username);
        request.setOldPassword(old_password);
        request.setNewPassword(new_password)
    
        try {
          const response = await state.userClient.changePassword(request, {'authorization': state.jwtToken});
          if (response == 1) {
            alert("Password Changed Successfully.");
            f7router.navigate('/');
          } else {
            if (response == "Invalid Password.") {
              alert("Old Password is invalid.  Please try again.")
            }
          }
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }

        update(state => state);
      },


      /***
       * Games
       */
      async getGameCount() {

        for (let i=0; i<4; i++) {
          
          let request = state.getGameCountRequest;
          request.setId(i);
      
          try {
            const response = await state.gameClient.getCount(request, {'authorization': state.jwtToken});

            switch(i) {
              case 0:
                state.gameCount.total = response; break;
              case 1:
                state.gameCount.draft = response; break;
              case 2:
                state.gameCount.published = response; break;
              case 3:
                state.gameCount.archived = response; break;
            }
          } catch (err) {
            alert(err.message);
            console.log(err);
            state.isLoggedIn = false;
          }
          
        }
        update(state => state);
        
      },

      async getGameList() {
          
        let request = state.gameListRequest;
        request.setLimit(20);
        request.setOffset(0);

        try {
          const response = await state.gameClient.list(request, {'authorization': state.jwtToken});

          state.games = [];
          for (let item of response.getResultList()) {
            state.games = [...state.games,  {
              id: item.getId(), 
              title: item.getTitle(), 
              subtitle: item.getSubtitle(), 
              img_url: item.getImgUrl(), 
              content: item.getContent(), 
              type_id: item.getTypeId(), 
              engine_id: item.getEngineId(), 
              version: item.getVersion(), 
              status: item.getStatus()
            }];
          }
            
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);
      
      },

      /***
       * Items
       */
      async getItemCount() {

        for (let i=0; i<4; i++) {
          
          let request = state.getItemCountRequest;
          request.setId(i);
      
          try {
            const response = await state.itemClient.getCount(request, {'authorization': state.jwtToken});

            switch(i) {
              case 0:
                state.itemCount.total = response; break;
              case 1:
                state.itemCount.draft = response; break;
              case 2:
                state.itemCount.published = response; break;
              case 3:
                state.itemCount.archived = response; break;
            }
          } catch (err) {
            alert(err.message);
            console.log(err);
            state.isLoggedIn = false;
          }
          
        }
        update(state => state);
        
      },

      async getItemList() {
          
        let request = state.itemListRequest;
        request.setLimit(20);
        request.setOffset(0);

        try {
          const response = await state.itemClient.list(request, {'authorization': state.jwtToken});

          state.items = [];
          for (let item of response.getResultList()) {
            state.items = [...state.items,  {
              id: item.getId(), 
              title: item.getTitle(), 
              subtitle: item.getSubtitle(), 
              img_url: item.getImgUrl(), 
              content: item.getContent(), 
              type_id: item.getTypeId(), 
              amount: item.getAmount(), 
              status: item.getStatus()
            }];
          }
            
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);
      
      },

      /***
       * Prizes
       */
      async getPrizeCount() {

        for (let i=0; i<4; i++) {
          
          let request = state.getPrizeCountRequest;
          request.setId(i);
      
          try {
            const response = await state.prizeClient.getCount(request, {'authorization': state.jwtToken});

            switch(i) {
              case 0:
                state.prizeCount.total = response; break;
              case 1:
                state.prizeCount.draft = response; break;
              case 2:
                state.prizeCount.published = response; break;
              case 3:
                state.prizeCount.archived = response; break;
            }
          } catch (err) {
            alert(err.message);
            console.log(err);
            state.isLoggedIn = false;
          }
          
        }
        update(state => state);
        
      },

      async getPrizeList() {
          
        let request = state.prizeListRequest;
        request.setLimit(20);
        request.setOffset(0);

        try {
          const response = await state.prizeClient.list(request, {'authorization': state.jwtToken});

          state.prizes = [];
          for (let item of response.getResultList()) {
            state.prizes = [...state.prizes,  {
              id: item.getId(), 
              title: item.getTitle(), 
              subtitle: item.getSubtitle(), 
              img_url: item.getImgUrl(), 
              content: item.getContent(), 
              type_id: item.getTypeId(), 
              reward_id: item.getRewardId(), 
              reward_amount: item.getRewardAmount(), 
              game_id: item.getGameId(), 
              status: item.getStatus()
            }];
          }
            
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);
      
      },


      /***
       * Tournaments
       */
      async getTournamentCount() {

        for (let i=0; i<4; i++) {
          
          let request = state.getTournamentCountRequest;
          request.setId(i);
      
          try {
            const response = await state.tournamentClient.getCount(request, {'authorization': state.jwtToken});
           
            switch(i) {
              case 0:
                state.tournamentCount.total = response; break;
              case 1:
                state.tournamentCount.draft = response; break;
              case 2:
                state.tournamentCount.published = response; break;
              case 3:
                state.tournamentCount.archived = response; break;
            }
          } catch (err) {
            alert(err.message);
            console.log(err);
            state.isLoggedIn = false;
          }
          
        }
        update(state => state);
        
      },

      async getTournamentList() {
          
        let request = state.tournamentListRequest;
        request.setLimit(20);
        request.setOffset(0);

        try {
          const response = await state.tournamentClient.list(request, {'authorization': state.jwtToken});

          state.tournaments = [];
          for (let item of response.getResultList()) {
            state.tournaments = [...state.tournaments,  {
              id: item.getId(),
              title: item.getTitle(),
              prize_id: item.getPrizeId(),
              game_id: item.getGameId(),
              status: item.getStatus(),
              scheduled_on: item.getScheduledOn()
            }];
          }
            
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);
      
      },


      /***
       * Winners
       */
      async getWinnerList() {
          
        let request = state.winnerListRequest;
        request.setLimit(20);
        request.setOffset(0);

        try {
          const response = await state.winnerClient.list(request, {'authorization': state.jwtToken}); 
          alert(response);
        } catch (err) {
          alert(err.message);
          console.log(err);
          state.isLoggedIn = false;
        }
        update(state => state);
      
      },

	}

	return {
		subscribe,
		set,
		update,
		...methods,
	};
}


export default dataClient()