<Page>
  <Navbar title={title}></Navbar>

  <BlockTitle>Game Informations</BlockTitle>
  
  <List noHairlinesMd>
    <ListInput
      label="Title"
      type="text"
      placeholder="title here"
      required
      validate

    ></ListInput>
    <ListInput
      label="Sub-title"
      type="text"
      placeholder="sub-title here"
      required
      validate

    ></ListInput>
    
    <ListInput
      label="Image URL"
      type="url"
      placeholder="image url with the base-filename, make sure the rest of 128, 256, 512, 1024 sizes are in the same folder of the url provided."
      required
      validate

    ></ListInput>

    <ListInput
      type="textarea"
      label="Game Code"
      placeholder="game code here"
      resizable
      required
      validate

    ></ListInput>
    <ListInput
      label="Game Version"
      type="text"
      placeholder="version here"
    ></ListInput>

    <ListInput
      label="Status"
      type="select"
      value="Draft"
      placeholder="Please choose..."
    >
      <option value="1">Draft</option>
      <option value="2">Published</option>
      <option value="3">Archived/Disabled</option>
    </ListInput>

    <BlockTitle>3D Engine</BlockTitle>
    <List>
      <ListItem
        radio
        radioIcon="start"
        title="PhaserJS v2"
        value="11"
        name="engine"
        checked
      ></ListItem>
      <ListItem
        radio
        radioIcon="start"
        title="PhaserJS v3"
        value="12"
        name="engine"
        checked
      ></ListItem>
      <ListItem
        radio
        radioIcon="start"
        title="PixiJS v5"
        value="21"
        name="engine"
      ></ListItem>
      
    </List>
    
  </List>

  <BlockTitle></BlockTitle>
  <Block strong>
    <Row tag="p">
      <Col><Button class="col" large fill raised color="red" animate={true} transition="f7-fade" on:click={doSave}>Save & Back</Button></Col>
      <Col><Button class="col" large fill raised color="green" back animate={false}>Cancel</Button></Col>
    </Row>
  </Block>

</Page>
<script>
  import {
    f7,
    ListItem,
    Col,
    Page,
    Navbar,
    List,
    ListInput,
    BlockTitle,
    Row,
    Button,
    Range,
    Toggle,
    Block
  } from 'framework7-svelte';

  export let f7route;
  const id = f7route.params.id;
  
  $: title = id > 0 ? "Edit Game" : "New Game";


  function doSave() {

    const isValid = f7.input.validateInputs('#gameForm');
    if (!isValid) {
      return;
    }

    f7router.navigate('/games/');
  }
</script>
